//import packages/libraries...
import React, { useEffect, useState } from 'react'
import { useNavigate, Link } from "react-router-dom";


//import Styles...
import { AvaContainer,Avatars,TitleDiv,ASButton, Avat } from './index.style'
import CircularProgress from '@mui/material/CircularProgress';

//import Local files...
import { FetchAvatars, setUserAvatar } from '../../Utility/AvatarFunctions';
import useMainStore from '../../Stores/0__MainStore';







function SelectAvatar() 
{

    /*------------------------  App`s Main Store ------------------------*/
  let {user,setUser} = useMainStore((state) => {return state}) //get all properties from center State

    const navigate = useNavigate();

    let savedUser = localStorage.getItem("User");
    if(!savedUser || !user){if(window.location.pathname !== '/login') navigate('/login') };
    if( user && Object.keys(user).length > 2 ? !user?.avatarImage.hasAvatar : false){if(window.location.pathname !== '/avatar-select'); navigate("/avatar-select")}
    if( user && Object.keys(user).length > 2 ? user?.avatarImage.hasAvatar : false){if(window.location.pathname !== '/') navigate('/')}
     

    /*------------------------ Function's main state ------------------------*/
    const [avatars, setAvatars] = useState([]);
    const [loading, setLoading] = useState(false);
    const [selectedAvatar, setSelectedAvatar] = useState(undefined);

    /*------------------------ Get Avatars ------------------------*/

    useEffect(() => {(async()=>{await FetchAvatars(setAvatars)})() ;return () => {}}, [])
    



  return (
    <>
        <AvaContainer>

            <TitleDiv>
                <h1>Pick an Avatar as your profile picture</h1>
            </TitleDiv>

            <Avatars>

                {avatars.map((avatar, i) => {

                return (
                    <Avat key={i}>
                        <img
                            src={`data:image/svg+xml;base64,${avatar}`}
                            alt="avatar"
                            key={avatar}
                            onClick={() => setSelectedAvatar([i,avatar])}
                            isselected={`${i == selectedAvatar?.[0] ? '0.4rem solid #4e0eff' : " "}`}
                            shouldscale={`${i == selectedAvatar?.[0] ? '1.6' : " "}`}
                        />
                    </Avat>
                );
                 })}

            </Avatars>

            <ASButton disabled={!selectedAvatar} isactive={ `${!selectedAvatar ? 'grayscale(1)' : " "}`} onClick={()=>{setUserAvatar(user,selectedAvatar,setUser,loading, setLoading)}}>
                 {loading ? <CircularProgress size={60} /> : 'Set as PROFILE Picture'}
            </ASButton>

        </AvaContainer>
    </>
  )
}





export default SelectAvatar